import React from "react";
import "./Problems.css";
import { easeInOut, motion } from "framer-motion";

export default function Problems() {
  const scrollAnimation = {
    hidden: { opacity: 0 },
    visible: (custom) => ({
      opacity: 1,
      transition: { duration: 0.8, ease: easeInOut },
    }),
  };
  return (
    <motion.section
      className="problems"
      initial="hidden"
      whileInView="visible"
      variants={scrollAnimation}
      viewport={{ once: true }}
    >
      <header>
        <h2>
          ,  
          <br />  
        </h2>
      </header>
      <div className="problems-list">
        <div className="problem-row">
          <div className="problem">
            <p>
              <span>01</span>   &nbsp;
              <br />
                
            </p>
            <div className="line"></div>
          </div>
          <div className="problem">
            <p>
              <span>05</span> &nbsp;
              <br />
              
            </p>
            <div className="line"></div>
          </div>
        </div>
        <div className="problem-row">
          <div className="problem">
            <p>
              <span>02</span> &nbsp;
              <br />
                
            </p>
            <div className="line"></div>
          </div>
          <div className="problem">
            <p>
              <span>06</span>    -
              <br />
               
            </p>
            <div className="line"></div>
          </div>
        </div>
        <div className="problem-row">
          <div className="problem">
            <p>
              <span>03</span>   
            </p>
            <div className="line"></div>
          </div>
          <div className="problem">
            <p>
              <span>07</span>  
            </p>
            <div className="line"></div>
          </div>
        </div>
        <div className="problem-row">
          <div className="problem">
            <p>
              <span>04</span> &nbsp;
              <br />
               ,&nbsp;
              <br />
              e   
            </p>
            <div className="line"></div>
          </div>
          <div className="problem">
            <p>
              <span>08</span> &nbsp;
              <br />
               &nbsp;
              <br />
               
            </p>
            <div className="line"></div>
          </div>
        </div>
      </div>
    </motion.section>
  );
}
