import React from "react";
import "./Services.css";
import Straumann from "../../assets/straumann.svg";
import Mis from "../../assets/mis.svg";
import Megagen from "../../assets/megagen.svg";
import Osstem from "../../assets/osstem.svg";
import { easeInOut, motion } from "framer-motion";

export default function Services() {
  const scrollAnimation = {
    hidden: { opacity: 0 },
    visible: (custom) => ({
      opacity: 1,
      transition: { duration: 0.8, ease: easeInOut },
    }),
  };
  return (
    <motion.section
      className="services"
      id="services"
      initial="hidden"
      whileInView="visible"
      variants={scrollAnimation}
      viewport={{ once: true }}
    >
      <header>
        <h2></h2>
      </header>
      <article className="services-list">
        <div className="services-row">
          <p></p>
          <p> 34 BYN</p>
        </div>
        <div className="services-row">
          <p> + 3D- ()</p>
          <p> 70 BYN</p>
        </div>
        <div className="services-row">
          <p> </p>
          <p> 940 BYN</p>
        </div>
        <div className="services-row">
          <p>   </p>
          <p> 1200 BYN</p>
        </div>
        <div className="services-row">
          <p>   </p>
          <p> 400 BYN</p>
        </div>
        <div className="services-row">
          <p>   </p>
          <p> 900 BYN</p>
        </div>
        <div className="services-row">
          <p>  /    </p>
          <p> 80 BYN</p>
        </div>
        <div className="services-row">
          <p>    (wax up)</p>
          <p> 35 BYN</p>
        </div>
        <figure className="labels">
          <img src={Straumann} alt="Straumann" />
          <img src={Mis} alt="Mis" />
          <img src={Megagen} alt="Megagen" />
          <img src={Osstem} alt="Osstem" />
        </figure>
      </article>
    </motion.section>
  );
}
