import React from "react";
import "./Education.css";
import Img1 from "../../assets/image 1.jpg";
import Img2 from "../../assets/image 2.jpg";
import Img3 from "../../assets/image 3.jpg";
import Img4 from "../../assets/image 4.jpg";
import { easeInOut, motion } from "framer-motion";

export default function Education() {
  const scrollAnimation = {
    hidden: { opacity: 0 },
    visible: (custom) => ({
      opacity: 1,
      transition: { duration: 0.8, ease: easeInOut },
    }),
  };
  const lineAnimation = {
    hidden: { scaleX: 0, opacity: 0 },
    visible: (custom) => ({
      scaleX: 1,
      opacity: 1,
      transition: { duration: 0.6, ease: easeInOut },
    }),
  };
  return (
    <motion.section
      className="education"
      initial="hidden"
      whileInView="visible"
      variants={scrollAnimation}
      viewport={{ once: true }}
    >
      <header>
        <h2></h2>
      </header>
      <div className="education-info">
        <article className="education-text">
            (   )
          2013 .     2019 .
          2020    - .
            , -,  ,
            .
        </article>
        <motion.div
          className="line"
          initial="hidden"
          whileInView="visible"
          variants={lineAnimation}
          viewport={{ once: true }}
          custom={2}
        ></motion.div>
        <article>
          <ul>
            <li>2015 .    </li>
            <li>2017 .      Z</li>
            <li>2019 .    </li>
            <li>
              2020 . Galip Gurel The concept of modern restoration: occlusion,
              personalised design, minimal invasion, pink aesthetics
            </li>
            <li>
              2022 .  . .,  . .  All-On-4   
                
            </li>
            <li>2023 .     </li>
            <li>2023 .    </li>
            <li>
              2024 .      
            </li>
            <li>
              2024 .  . .     
              
            </li>
          </ul>
        </article>
        <motion.div
          className="line"
          initial="hidden"
          whileInView="visible"
          variants={lineAnimation}
          viewport={{ once: true }}
          custom={10}
        ></motion.div>
        <figure className="education-imgs">
          <img src={Img1} alt="- " loading="lazy" />
          <img src={Img2} alt="   " loading="lazy" />
          <img
            src={Img3}
            alt="      "
            loading="lazy"
          />
          <img src={Img4} alt=" " loading="lazy" />
        </figure>
        <motion.div
          className="line"
          initial="hidden"
          whileInView="visible"
          variants={lineAnimation}
          viewport={{ once: true }}
          custom={2}
        ></motion.div>
      </div>
    </motion.section>
  );
}
